  height: 13px;
  width: 13px;
  margin-left: 4px;
  margin-right: 4px;
}

.catalogoDesk {
  width: 100%;
  height: auto;
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-y: scroll;
}
.catalogoDesk .contDesk {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding: 2em 0em;
  gap: 2em;
}
.catalogoDesk .contDesk .title-cont {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.catalogoDesk .contDesk .title-cont h1 {
  color: #1e7285;
  font-weight: 100;
  font-size: 2.2em;
  text-align: center;
}
.catalogoDesk .contDesk .conten-cats {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2em;
}
.catalogoDesk .contDesk .conten-cats .cardin {
  width: 16em;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  padding: 1em 0em;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transition: 0.4s all ease-in-out;
  gap: 1em;
}
.catalogoDesk .contDesk .conten-cats .cardin .cat-name {
  color: #00a3c7;
  font-weight: 100;
  text-align: center;
  padding: 0em 1em;
}
.catalogoDesk .contDesk .conten-cats .cardin .contImg {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;
}
.catalogoDesk .contDesk .conten-cats .cardin .contImg img {
  width: 100%;
  max-height: 15em;
}
.catalogoDesk .contDesk .conten-cats .cardin .pric {
  color: #222;
  font-weight: 100;
  text-align: center;
  padding: 0em 1em;
}
.catalogoDesk .contDesk .conten-cats .cardin:hover {
  box-shadow: rgba(23, 171, 202, 0.5) 0px 6px 24px;
}

@media (min-width: 992px) {
  .display-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  ion-content {
    display: none;
  }
  ion-header {
    display: none;
  }
  .catalogoDesk {
    display: flex;
  }
}
.fixin {
  width: 100%;
  height: 8.5em;
  margin: 0;
  padding: 0em 0.5em;
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 0.9em;
  overflow-wrap: break-word;
  overflow: hidden;
}

.catalogoDesk::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

.catalogoDesk::-webkit-scrollbar-thumb {
  background-color: rgba(0, 162, 199, 0.4745098039);
  width: 0.25em;
