import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from '@ionic/angular';
import Fuse from 'fuse.js';
import { ApiProductosService } from 'src/app/services/api-productos.service';
import { CarritoServiceService } from 'src/app/services/carrito-service.service';
import { ModalDescripcionPaquetesPage } from '../modal-descripcion-paquetes/modal-descripcion-paquetes.page';

@Component({
  selector: 'app-catalogo-paquetes',
  templateUrl: './catalogo-paquetes.page.html',
  styleUrls: ['./catalogo-paquetes.page.scss'],
})
export class CatalogoPaquetesPage implements OnInit {

  public page = 1;
  perPage = 10;
  totalData = 0;
  totalPage = 10;
  public totalItems: number = 0;
  public product: any;
  public searchbarVisible: boolean = false;
  public txtBuscador: string = '';
  public productos: any = [];
  public resultadoBusqueda: any = null;
  public numeroResultados: number = 0;
  productosCatalogo: any;
  precio: any;
  id_product : any;
  cp: number = 1;
  id_cat: any;
  public options = {
    // isCaseSensitive: false,
    // includeScore: false,
    shouldSort: true,
    // includeMatches: false,
    // findAllMatches: false,
    minMatchCharLength: 5,
    // location: 0,
    threshold: 0.5,
    distance: 60,
    // useExtendedSearch: false,
    // ignoreLocation: false,
    // ignoreFieldNorm: false,
    // fieldNormWeight: 1,
    keys: ['nombre'],
  }
  constructor(private productoService: ApiProductosService, public modalCtrl: ModalController,private activatedRoute:ActivatedRoute,
    private shoppingCart: CarritoServiceService) {
    
   }

  ngOnInit() {
    this.activatedRoute.params.subscribe(params=>{
      this.id_cat = params.id_categoria;
    })
    this.shoppingCart.cartItems.subscribe(d=>{
      this.totalItems = d.length;
    });
    this.getArticulos(this.id_cat);
  }

  openSearchBar() {
    var buscador = document.getElementById('buscador');
    if(buscador){
      buscador.style.display = "block";
    }
    this.txtBuscador = '';
    this.searchbarVisible = this.searchbarVisible == false ? true : false;
    if (this.searchbarVisible == false){
      buscador.style.display = "none";
    }
  }

  search(event: any) {
    if (event.detail.value != '') {
      const filtro = (event.target as HTMLInputElement).value.trim();
      const fuse = new Fuse(this.productos, this.options);
      
      const pattern = filtro;
      this.resultadoBusqueda = fuse.search(pattern);
      this.numeroResultados = fuse.search(pattern).length;
      this.resultadoBusqueda = this.resultadoBusqueda.slice(0, 100);
    }
  }

  getArticulos(id_cat){
    this.productoService.getCategorias(id_cat).subscribe(data =>{
      this.productosCatalogo = data;
      this.totalData = this.productosCatalogo.length;
      this.precio = this.productosCatalogo.precio;
      this.id_product = this.productosCatalogo.id_product;
    })
  }

  async modalDescripcion(id_product) {
    const modal = await this.modalCtrl.create({
      component: ModalDescripcionPaquetesPage,
      componentProps:{
        id_product: id_product
      },
      cssClass: 'modalRegistroCss',
      initialBreakpoint: 1,
      breakpoints: [0, 0.5, 1]
    });
    return await modal.present();
  }

}
